.Navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row-reverse;
      flex-direction: row-reverse;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 250px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.Navbar__link {
  position: relative;
  font-size: 20px;
  color: #424242;
  text-decoration: none;
  text-transform: uppercase;
}

.Navbar__link:hover {
  color: #ff5722;
}

.Navbar__link:after {
  position: absolute;
  top: 46px;
  right: 0;
  width: 0;
  height: 4px;
  background: #ff5722;
  content: "";
  -webkit-transition: width .3s linear;
  -o-transition: width .3s linear;
  transition: width .3s linear;
}

.Navbar__link:hover:after {
  width: 100%;
}



