.Form {
    padding: 16px;
    -ms-flex: 0 0 37%;
        flex: 0 0 37%;
    text-align: center;
    border-radius: 2px;
    -webkit-box-shadow: 0 0 2px 2px rgba(33,33,33,.5);
            box-shadow: 0 0 2px 2px rgba(33,33,33,.5);
}
.Form__label {
    display: block;
    margin-bottom: 20px;
    color: #424242;
    text-align: left;
    text-transform: capitalize;
}
.Form__input, .Form__textarea, .Form__select {
    display: block;
    width: 100%;
    margin-top: 4px;
    padding: 4px;
    font-size: 20px;
    color: #424242;
}
.Form__textarea {
    resize: none;
}
.ButtonSubmit {
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    text-transform: uppercase;
    background-color: #3f51b5;
    outline: 0;
    border: 0;
    border-radius: 2px;
    cursor: pointer;
    -webkit-box-shadow: 0 3px 3px 0 rgba(0,0,0,.3);
            box-shadow: 0 3px 3px 0 rgba(0,0,0,.3);
    -webkit-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear;
}
.ButtonSubmit:hover {
    background-color: #ff5722;
    -webkit-box-shadow: 0 5px 5px 0 rgba(0,0,0,.3);
            box-shadow: 0 5px 5px 0 rgba(0,0,0,.3);
}


